<template>
  <form @submit="formSubmit" enctype='multipart/form-data' class="container" style=" margin:0 auto; max-width: 1050px;">
    <md-card>
      <md-card-header
        class="md-card-header-icon"
        :class="getClass(headerColor)"
      >
        <div class="card-icon">
          <md-icon>perm_identity</md-icon>
        </div>
        <h4 class="title" v-text="$ml.with('VueJS').get('EditEstablishment')">
          
        </h4>
      </md-card-header>

      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-size-20 md-xsmall-size-100">
              <div class="picture-container">
                <div v-if="!imageRegular">
                  <div class="picture-container">
                    <img :src="logo" title="" />
                  </div>
                </div>
                <div class="image-container" v-else>
                  <img :src="imageRegular" />
                </div>
                <div class="button-container">
                  <md-button class="md-info md-round md-fileinput">
                    <template v-if="!imageRegular"
                      >Editar Logotipo</template
                    >
                    <template v-else
                      >Cambiar</template
                    >
                    <md-input type="file" @change="onFileChange"  />
                  </md-button>
                </div>
              </div>
            </div>
          <div class="md-layout-item md-small-size-100 md-size-40">
            <md-field>
              <label>Nombre</label>
              <md-input v-model="name" @change="formSubmit" name="name" type="text"></md-input>
            </md-field>
            <md-field>
              <label>Ubicacion <small>coordenadas</small></label>
              <md-input v-model="location" @change="formSubmit" name="location" type="text"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-40">
            <md-field>
              <label>Direccin</label>
              <md-input v-model="address" @change="formSubmit" name="address" type="text"></md-input>
            </md-field>
            <md-field>
              <label>Capacidad</label>
              <md-input v-model="capacity" @change="formSubmit" name="capacity" type="number"></md-input>
            </md-field>
          </div>
          </div>            
      
          <div class="md-layout-item md-size-100 text-right">
            <md-button  v-on:click="goToEstablishments" type="submit" class="md-raised md-success mt-4"
              >Regresar a Establecimientos</md-button
            >
          </div>
        
      </md-card-content>
    </md-card>
  </form>
</template>
<script>
export default {
  name: "edit-profile-form",
  props: {
    headerColor: {
      type: String,
      default: "",
    },
    regularImg: {
      type: String,
      default: "./img/image_placeholder.jpg"
    }
  },
  data() {
    return {
      establishmentId: this.$route.query.id,
      name: null,
      location: null,
      address: null,
      logo: this.imageRegular,
      capacity: null,
      imageRegular: "",
      file: "",
      logo: "",
      originalForm: [],
      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      },
    };
  },
  mounted(){
    
    this.axios.get('https://cors-anywhere.herokuapp.com/https://nameless-coast-31577.herokuapp.com/api/establishments/'+this.establishmentId,{
    //this.axios.get('http://localhost:8000/api/establishments/'+this.establishmentId,{
        headers: {
                    'Authorization': 'Bearer '+ window.$cookies.get('access_token')
                 },
      })
      .then((response) => {
       
       //console.log(response);
       this.originalForm.logo = "https://nameless-coast-31577.herokuapp.com/"+response.data.data.logo;
       this.originalForm.logoPath = response.data.data.logo;
       this.originalForm.name = response.data.data.name;
       this.originalForm.address = response.data.data.address;
       this.originalForm.location = response.data.data.location;
       this.originalForm.capacity = response.data.data.capacity;
       this.originalForm.establishmentId = response.data.data.establishmentId;
       this.logo = "https://nameless-coast-31577.herokuapp.com/"+response.data.data.logo;
       this.logoPath = response.data.data.logo;
       this.name = response.data.data.name;
       this.address = response.data.data.address;
       this.location = response.data.data.location;
       this.capacity = response.data.data.capacity;
       this.establishmentId = response.data.data.establishmentId;
        console.log(response.data.data.logo);
      }).catch(function (error) {
        // handle error
        console.log(error);
      });
  },
  methods: {
    getClass: function(headerColor) {
      return "md-card-header-" + headerColor + "";
    },
    handlePreview(file) {
      this.model.imageUrl = URL.createObjectURL(file.raw);
    },
    getError(fieldName) {
      return this.errors.first(fieldName);
    },
    validate() {
      return this.$validator.validateAll().then(res => {
        this.$emit("on-validated", res);
        return res;
      });
    },
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      if (e.target.name) {
        this.createImage(files[0], "circle");
      } else {
        this.createImage(files[0]);
        this.file = files[0];
        this.formSubmit(e);
      }
    },
    createImage(file, type) {
      let reader = new FileReader();
      let vm = this;

      reader.onload = e => {
        if (type === "circle") {
          vm.imageCircle = e.target.result;
        } else {
          vm.imageRegular = e.target.result;
        }
      };
      reader.readAsDataURL(file);
    },
    removeImage: function(type) {
      if (type === "circle") {
        this.imageCircle = "";
      } else {
        this.imageRegular = "";
      }
    },
    removeImage: function(type) {
      if (type === "circle") {
        this.imageCircle = "";
      } else {
        this.imageRegular = "";
      }
    },
    onResponsiveInverted() {
      if (window.innerWidth < 768) {
        this.responsive = true;
      } else {
        this.responsive = false;
      }
    },
    formSubmit(e) {
                let reader = new FileReader();
                e.preventDefault();
                let currentObj = this;
                let formData = new FormData();
                formData.append('_method', 'PATCH');
                
                if(this.file)
                formData.append('logo', this.file);
                
                if(this.originalForm.name != this.name)
                formData.append('name', this.name);

                if(this.originalForm.location != this.location)
                formData.append('location', this.location);

                if(this.originalForm.address != this.address)
                formData.append('address', this.address);
                
                if(this.originalForm.capacity != this.capacity)
                formData.append('capacity', this.capacity);


                 for (var key of formData.entries()) {
                    console.log(key[0] + ', ' + key[1])
                  }
                  
this.axios.post('https://cors-anywhere.herokuapp.com/https://nameless-coast-31577.herokuapp.com/api/establishments'+this.establishmentId, formData, {  
               // this.axios.post('http://localhost:8000/api/establishments/'+this.establishmentId, formData, {
                    headers: {
                    'Content-Type': 'multipart/form-data',
                    'Authorization': 'Bearer '+ window.$cookies.get('access_token')
                    },
                })
                .then(function (response) {
                    console.log(response);
                    var color = Math.floor(Math.random() * 4 + 1);
                    currentObj.$notify({
                      message:
                        "El establecimiento fue modificado.",
                      icon: "add_alert",
                      horizontalAlign: 'center',
                      verticalAlign: 'top',
                      type: currentObj.type[color]
                    });
                })
                .catch(function (error) {
                    console.log(error);
                });
      },

      goToEstablishments(){
        this.$router.push({name: 'Establecimientos'});
      }
  }
};
</script>
<style>
  .file-input{
    max-width: 100%;

  }
</style>
