<template>
  <form @submit="formSubmit" enctype='multipart/form-data' class="container" style=" margin:0 auto; max-width: 1050px;">
    <md-card>
      <md-card-header
        class="md-card-header-icon"
        :class="getClass(headerColor)"
      >
        <div class="card-icon">
          <md-icon>view_list</md-icon>
        </div>
        <h4 class="title">
          Agregar establecimiento - <small>Ingrese los datos</small>
        </h4>
      </md-card-header>

      <md-card-content>
        <div class="md-layout">
          <div class="md-layout-item md-size-20 md-xsmall-size-100">
              <div class="file-input">
                <div v-if="!imageRegular">
                  <div class="image-container">
                    <img :src="regularImg" title="" />
                  </div>
                </div>
                <div class="image-container" v-else>
                  <img :src="imageRegular" />
                </div>
                <div class="button-container">
                  <md-button
                    class="md-danger md-round"
                    @click="removeImage"
                    v-if="imageRegular"
                    ><i class="fa fa-times"></i>quitar</md-button
                  >
                  <md-button class="md-success md-round md-fileinput">
                    <template v-if="!imageRegular"
                      >Subr Logotipo</template
                    >
                    <template v-else
                      >Cambiar</template
                    >
                    <md-input type="file" @change="onFileChange"  />
                  </md-button>
                </div>
              </div>
            </div>
          <div class="md-layout-item md-small-size-100 md-size-40">
            <md-field>
              <label>Nombre</label>
              <md-input v-model="name" name="name" type="text"></md-input>
            </md-field>
            <md-field>
              <label>Ubicacion <small>coordenadas</small></label>
              <md-input v-model="location" name="location" type="text"></md-input>
            </md-field>
          </div>
          <div class="md-layout-item md-small-size-100 md-size-40">
            <md-field>
              <label>Direccin</label>
              <md-input v-model="address" name="address" type="text"></md-input>
            </md-field>
            <md-field>
              <label>Capacidad</label>
              <md-input v-model="capacity" name="capacity" type="number"></md-input>
            </md-field>
          </div>
          </div>            
      
          <div class="md-layout-item md-size-100 text-right">
            <md-button type="submit" class="md-raised md-success mt-4"
              >Crear Establecimiento</md-button
            >
          </div>
        
      </md-card-content>
    </md-card>
  </form>
</template>
<script>
export default {
  name: "edit-profile-form",
  props: {
    headerColor: {
      type: String,
      default: "",
    },
    regularImg: {
      type: String,
      default: "./img/image_placeholder.jpg"
    }
  },
  data() {
    return {
      name: null,
      location: null,
      address: null,
      logo: this.imageRegular,
      capacity: null,
      imageRegular: "",
      file: "",
      type: ["", "info", "success", "warning", "danger"],
      notifications: {
        topCenter: false
      },
    };
  },
  methods: {

    notifyVue(verticalAlign, horizontalAlign) {
      var color = Math.floor(Math.random() * 4 + 1);
      this.$notify({
        message:
          "Se guardo el establecimiento.",
        icon: "add_alert",
        horizontalAlign: horizontalAlign,
        verticalAlign: verticalAlign,
        type: this.type[color]
      });
    },

    base64ToBlob(base64, mime) 
      {
          mime = mime || '';
          var sliceSize = 1024;
          var byteChars = window.atob(base64);
          var byteArrays = [];

          for (var offset = 0, len = byteChars.length; offset < len; offset += sliceSize) {
              var slice = byteChars.slice(offset, offset + sliceSize);

              var byteNumbers = new Array(slice.length);
              for (var i = 0; i < slice.length; i++) {
                  byteNumbers[i] = slice.charCodeAt(i);
              }

              var byteArray = new Uint8Array(byteNumbers);

              byteArrays.push(byteArray);
          }

          return new Blob(byteArrays, {type: mime});
      },
     base64MimeType (encoded) {
      var result = null;

      if (typeof encoded !== 'string') {
        return result;
      }

      var mime = encoded.match(/data:([a-zA-Z0-9]+\/[a-zA-Z0-9-.+]+).*,.*/);

      if (mime && mime.length) {
        result = mime[1];
      }

      return result;
    },
    getClass: function(headerColor) {
      return "md-card-header-" + headerColor + "";
    },
    handlePreview(file) {
      this.model.imageUrl = URL.createObjectURL(file.raw);
    },
    getError(fieldName) {
      return this.errors.first(fieldName);
    },
    validate() {
      return this.$validator.validateAll().then(res => {
        this.$emit("on-validated", res);
        return res;
      });
    },
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      if (e.target.name) {
        this.createImage(files[0], "circle");
      } else {
        this.createImage(files[0]);
        this.file = files[0];
      }
    },
    createImage(file, type) {
      let reader = new FileReader();
      let vm = this;

      reader.onload = e => {
        if (type === "circle") {
          vm.imageCircle = e.target.result;
        } else {
          vm.imageRegular = e.target.result;
        }
      };
      reader.readAsDataURL(file);
    },
    removeImage: function(type) {
      if (type === "circle") {
        this.imageCircle = "";
      } else {
        this.imageRegular = "";
      }
    },
    onResponsiveInverted() {
      if (window.innerWidth < 768) {
        this.responsive = true;
      } else {
        this.responsive = false;
      }
    },
    formSubmit(e) {
                let reader = new FileReader();
                e.preventDefault();
                let currentObj = this;
               // var base64ImageContent = this.imageRegular.replace(/^data:image\/(png|jpg);base64,/, "");
                //var blob = this.base64ToBlob(base64ImageContent, 'image/png');  
                //console.log(blob);
                let formData = new FormData();
                formData.append('logo', this.file);
                formData.append('name', this.name);
                formData.append('location', this.location);
                formData.append('address', this.address);
                formData.append('capacity', this.capacity);
                formData.append('_METHOD', 'post');
                // console.log(this.name);
                // console.log(this.location);
                // console.log(this.address);
                for (var key of formData.entries()) {
      console.log(key[0] + ', ' + key[1])
    }
                // console.log(this.base64MimeType(this.imageRegular)); 
                // console.log(this.capacity);
                this.axios.post('https://cors-anywhere.herokuapp.com/https://nameless-coast-31577.herokuapp.com/api/establishments', formData, {
                //this.axios.post('http://localhost:8000/api/establishments/', formData, {
                    headers: {
                    'Content-Type': 'multipart/form-data',
                    'Authorization': 'Bearer '+ window.$cookies.get('access_token')
                    }
                })
                .then(function (response) {
                    console.log(response);
                    var color = Math.floor(Math.random() * 4 + 1);
                    currentObj.$notify({
                      message:
                        "El establecimiento "+currentObj.name+" fue agregado.",
                      icon: "add_alert",
                      horizontalAlign: 'center',
                      verticalAlign: 'top',
                      type: currentObj.type[color]
                    });
                    currentObj.$router.push({name: 'Establecimientos'});
                    //window.location.href ="#/superadmin/establecimientos"
                })
                .catch(function (error) {
                    console.log(error);
                });
      }
  }
};
</script>
<style>
  .file-input{
    max-width: 100%;

  }
</style>
